.wrapper {
  height: 100vh;
  width: 100vw;
  background: var(--main-bg-color);
  display: flex;
}
.formRegister {
  background-color: white;
  margin: auto;
  width: 80%;
  padding: 1em 1em;
  text-align: center;
  border-radius: 4px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}
.formRegister h2 {
  color: #617bb0;
  font-size: 2.2rem;
  padding: 1em 2em;
}
.formRegister p {
  margin-bottom: 1em;
}
.inputForm {
  font-size: 1rem;
  width: 100%;
  display: inline-block;
  margin-left: auto;
  margin-right: auto;

  padding: 5px 5px;
}

.inputForm::placeholder {
  font-size: 0.8rem;
  color: rgba(117, 117, 117, 0.3);
}
.inputDesc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5em;
}
.inputError {
  font-size: 0.7rem;
  color: red;
}
.inputSuccess {
  font-size: 0.7rem;
  color: green;
}
.formButton {
  width: 60%;
  font-size: 1.1rem;
  padding: 0.5em 1em;
  background: var(--main-bg-color);
  color: white;
  border: none;
  border-radius: 4px;
  margin: 1em auto;
}
.formButton:hover {
  background: linear-gradient(#39455c, #415a6f);
}
.formLink {
  text-decoration: none;
  color: #617bb0;
  font-size: 1rem;
  font-weight: 600;
}
.modalRegister {
  width: 100%;
  padding: 1em 1em;
  text-align: center;
  border-radius: 4px;
}

@media screen and (min-width: 768px) {
  .formRegister {
    width: 50%;
  }
  .formRegister h2 {
    font-size: 2.5rem;
  }
  .formRegister input {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 1000px) {
  .formRegister {
    width: 30%;
  }
  .formRegister h2 {
    font-size: 2.5rem;
  }
  .formRegister input {
    font-size: 1.2rem;
  }
}
